* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  text-decoration: none;
  scroll-behavior: smooth;
}

section {
  width: 100%;
  padding: 5rem 3rem;
}

.section-unpair {
  background-color: #fff;
}

.section-pair {
  background-color: #f5f5f5;
}

.container {
  max-width: 50rem;
  height: 100%;
  margin: auto;
}

.sec-header {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sec-main {
  margin-top: 3rem;
  text-align: center;
  font-size: 1.1rem;
}

svg {
  fill: #8c5f41;
  stroke: #8c5f41;
}

.title {
  text-align: center;
  margin-top: 1rem;
  font-weight: 600;
  font-size: 2rem;
  color: #8c5f41;
  text-transform: uppercase;
}

.m-top-1 {
  margin-top: 1rem;
}

.m-top-2 {
  margin-top: 2rem;
}

.m-top-3 {
  margin-top: 3rem;
}

.btn, button {
  color: #8c5f41;
  margin-top: 3rem;
  padding: 1rem 2rem;
  border: 3px solid #8c5f41;
  border-radius: 2px;
  cursor: pointer;
  background-color: transparent;
  font-size: 1rem;
  display: block;
  margin: auto;
  width: 15rem;
}

.btn:hover, button:hover {
  background-color: #8c5f41;
  color: #fff;
}

@media (max-width: 375px) {
  section {
    padding: 5rem 1rem;
  }
}