.sec-main .message {
  font-family: 'Alex Brush', cursive;
  font-size: 2rem;
  text-align: center;
}

.sec-main .salmo {
  text-align: right;
  margin: 1rem 2rem;
  font-size: 1.1rem;
}

.sec-main .gretings {
  margin-top: 3rem;
  font-size: 1.3rem;
  font-weight: 300;
  text-align: center;
}

section .img_divider_container {
  display: flex;
  justify-content: center;
  margin: 2rem 0 0;
}

section .img_divider {
  padding: 0.5rem;
  max-width: 25rem;
  max-height: 25rem;
  object-fit: cover;
  --s: 10px;
  padding: var(--s);
  border: calc(2*var(--s)) solid #0000;
  outline: 1px solid #000;
  outline-offset: calc(-1*var(--s));
  background: conic-gradient(from 90deg at 1px 1px,#0000 25%,#000 0);
}

@media (max-width: 500px) {
  section .img_divider {
    max-width: 20rem;
    max-height: 20rem;
  }
}

@media (max-width: 360px) {
  section .img_divider {
    max-width: 15rem;
    max-height: 15rem;
  }
}