.navbar .logo {
  font-size: 2rem;
  font-weight: 300;
}

.navbar {
  box-shadow: 0 0 10px #0003;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 1rem 6rem;
  position: fixed;
  top: 0;
  left: 0;
  height: 5rem;
  width: 100%;
  z-index: 200;
}

.navbar ul {
  font-size: .9rem;
  text-transform: uppercase;
  font-weight: 400;
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 2rem;
}

.navbar ul a {
  color: #000;
}

.navbar ul a:hover {
  font-weight: 700;
}

.navbar span {
  display: none;
  cursor: pointer;
}

.navbar span svg {
  stroke: #474747;
}

@media (max-width: 1100px) {
  .navbar ul {
    display: none;
  }

  .navbar span {
    display: flex;
  }
}

@media (max-width: 550px) {
  .navbar {
    padding: 1rem 3rem;
  }
}

@media (max-width: 350px) {
  .navbar .logo {
    font-size: 1.3rem;
  }
}