.home {
  height: 100vh;
}

.home-info {
  color: #fff;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
}

.cover_img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
}

.married {
  display: flex;
  flex-direction: column;
  font-size: 5rem;
  text-align: center;
}

.married span {
  font-family: 'Alex Brush', cursive;
  line-height: .9;
}

.countdown {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
}

.countdown div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.countdown p, .countdown .divider {
  font-size: 3rem;
  font-weight: 300;
}

.countdown span {
  text-align: center;
}

@media (max-width: 375px) {
  .home-info {
    gap: 1rem;
  }
  .married {
    font-size: 4rem;
  }
  .countdown {
    flex-wrap: wrap;
    gap: 0;
  }
  .countdown p, .countdown .divider {
    font-size: 2rem;
    font-weight: 300;
  }
}

@media (max-width: 300px) {
  .married {
    font-size: 3rem;
  }
}