.reserved {
  font-size: 1.3rem;
}

.rsvp-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}

.rsvp #name {
  width: 70%;
  padding: .5rem;
  text-align: center;
  border: 2px solid #8c5f41;
  margin: 0.5rem 0 0;
}

.rsvp label, .rsvp input {
  font-size: 1.2rem;
}

.rsvp ul {
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: space-between;
}

.rsvp li {
  width: 100%;
  font-size: 1.1rem;
}

.rsvp .radio {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  margin-right: 10px;
}

.rsvp .radio__input {
  display: none;
}

.rsvp .radio__radio {
  width: 1.5rem;
  height: 1.5rem;
  border: 2px solid #d8e4e2;
  border-radius: 50%;
  margin-right: 10px;
  padding: 2px;
}

.rsvp .radio__radio::after {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  background: #8c5f41;
  border-radius: 50%;
  transform: scale(0);
  transition: transform 0.1s;
}

.rsvp .radio__input:checked + .radio__radio::after {
  transform: scale(1);
}

.rsvp select {
  border: 2px solid #8c5f41;
}

.rsvp textarea {
  font-size: 1.2rem;
  padding: .5rem;
  width: 70%;
  height: 5rem;
  margin: 1rem 0 3rem;
  border: 2px solid #8c5f41;
  resize: none;
}

.rsvp #guest_number {
  display: none;
}

.rsvp #guest_number {
  width: 70%;
}

.rsvp select {
  padding: 0.5rem;
  width: 100%;
  font-size: 1.2rem;
  text-align: center;
}

.rsvp .loading {
  display: none;
  background-color: #37302C;
  color: #fff;
  border: 2px solid #fff;
}

.rsvp .submit_button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 54px;
}

.rsvp svg { 
  display: none;
}

.rsvp .sending {
  background-color: #8c5f41;
  color: #fff;
}

.rsvp .sending svg {
  display: block;
  fill: #fff;
  margin-right: .5rem;
  animation: rotate_right 1s linear infinite;
}

@keyframes rotate_right {
  from {
    transform: rotateZ(0);
  }
  to {
      transform: rotateZ(360deg);
  }
}

@media (max-width: 760px) {
  .rsvp select, .rsvp #name, .rsvp textarea, .rsvp #guest_number {
    width: 100%;
  }
}

@media (max-width: 650px) {
  .rsvp ul {
    flex-direction: column;
    gap: 2rem;
  }
}

@media (max-width: 310px) {
  .rsvp ul li label {
    display: flex;
    flex-direction: column;
    gap: .3rem;
  }
}