.date .cls-1 {
  stroke-miterlimit: 10;
  stroke-width: 11px;
}

.date .cls-1, .cls-2 {
  fill: none;
  stroke: #8c5f41;
}

.date .cls-2 {
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 15px;
}

.date h2 {
  font-family: 'Alex Brush', cursive;
  font-size: 3rem;
  font-weight: 500;
}